.about .about-list,
.about .about-list ul {
  list-style: circle;
  margin-left: 20px;
}
.about .about-list li,
.about .about-list ul li {
  margin-top: 6px;
}
.about .icon-email {
  margin-right: 6px;
  vertical-align: middle;
}
@keyframes me {
  0% {
    background: yellow;
  }
  100% {
    background: pink;
  }
}
@-webkit-keyframes me {
  0% {
    background: yellow;
  }
  100% {
    background: pink;
  }
}
.about .code {
  width: 70px;
  position: relative;
  -webkit-animation: me 3s infinite ease-in-out alternate;
  animation: me 3s infinite ease-in-out alternate;
}
.about .link {
  position: relative;
  text-decoration: none;
  color: #121314;
}
.about .link:after {
  content: "";
  position: absolute;
  top: 60%;
  left: -0.1em;
  right: -0.1em;
  bottom: 0;
  transition: top 200ms cubic-bezier(0, 0.8, 0.13, 1);
  background-color: rgba(79, 192, 141, 0.5);
}
.about .link:hover:after {
  top: 0%;
}
